import type React from 'react';
import Image from 'next/image';
import type { AdBannerItem } from '@/types';
import { cn } from '@/lib/utils';

interface AdBannerSectionProps {
  ad: AdBannerItem;
}

const AdBannerSection: React.FC<AdBannerSectionProps> = ({ ad }) => {
  // Desktop specific classes
  const desktopAspectRatioClass = ad.aspectRatio === '3.4:0.7' ? 'aspect-[3.4/0.7]' : 'aspect-[3.4/1.2]';
  const desktopMaxHClass = ad.aspectRatio === '3.4:0.7' ? 'max-h-[250px]' : 'max-h-[400px]';
  
  // Mobile specific aspect ratio based on ad.aspectRatio prop
  const mobileImageAspectRatioClass = ad.aspectRatio === '3.4:0.7' ? 'aspect-[34/7]' : 'aspect-[17/6]';

  return (
    <section className="my-8 sm:my-12 text-foreground">
      {/* Mobile Layout */}
      <div className="md:hidden">
        <div className={cn(
          "relative w-full overflow-hidden",
          mobileImageAspectRatioClass
        )}>
          <Image
            src={ad.imageUrl}
            alt={ad.title}
            fill
            className="object-cover"
            data-ai-hint={ad.imageHint}
          />
        </div>
      </div>
      
      {/* Desktop Layout */}
      <div className={cn(
        "hidden md:block relative w-full h-auto text-foreground", 
        desktopAspectRatioClass, 
        desktopMaxHClass
      )}>
        <Image
          src={ad.imageUrl}
          alt={ad.title}
          fill
          className="object-cover"
          data-ai-hint={ad.imageHint}
        />
        {/* Overlay */}
        <div className="absolute inset-0 flex items-center left-0">
          <div className="bg-black/60 p-8 rounded-lg ml-12 max-w-xl">
            <h2 className="text-3xl font-bold text-yellow-400 mb-2">{ad.title}</h2>
            {ad.description && <p className="text-white text-lg mb-4">{ad.description}</p>}
            {ad.ctaText && (
              <button className="flex items-center gap-2 bg-yellow-400 text-black font-semibold px-6 py-2 rounded hover:bg-yellow-300 transition">
                <svg width="22" height="22" fill="none" stroke="currentColor"><polygon points="6,4 20,11 6,18" /></svg>
                {ad.ctaText}
              </button>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};

export default AdBannerSection;
